t-decoration: underline;\n      color: #4e944f;\n    }\n  }\n}\n\n.hero {\n  min-height: 250px;\n  width: 100%;\n  text-align: center;\n\n  img {\n    width: 100%;\n    max-height: 30rem;\n    object-fit: cover;\n    position: relative;\n  }\n  &__inner {\n    top: 0;\n    left: 0;\n    margin-left: 25%;\n    margin-top: 25%;\n    width: 50%;\n    padding: 10px;\n    position: absolute;\n    background-color: rgb(0, 0, 0);\n    background-color: rgba(0, 0, 0, 0.4);\n  }\n\n  &__title {\n    color: #fff;\n    font-weight: 400px;\n    font-size: 30px;\n  }\n}\n\nmain {\n  width: 100%;\n  margin: 0 auto;\n}\n\n.content {\n  padding: 32px;\n}\n\n#restaurant-label {\n  text-align: center;\n  font-size: 35px;\n}\n/*\n * restaurant\n */\n\n#explore-restaurant-list {\n  margin: 32px auto auto;\n  text-align: left;\n  display: grid;\n}\n\n.restaurant-item {\n  overflow: hidden;\n  margin: 10px;\n  background-color: #e9efc0;\n  border-radius: 8px;\n  box-shadow: rgba(10, 20, 30, 0.26) 0px 1px 20px;\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: center;\n  img {\n    width: 100%;\n    height: 200px;\n  }\n  h2 {\n    padding: 10px;\n  }\n\n  a {\n    display: inline-block;\n    color: black;\n    text-decoration: none;\n    min-width: 44px;\n    min-height: 44px;\n  }\n}\n\n.city {\n  margin-top: 5px;\n  font-weight: 400;\n  padding-left: 10px;\n  font-weight: 600;\n}\n\n.rating {\n  margin-top: 5px;\n  font-weight: 300;\n  padding-left: 10px;\n  font-weight: 500;\n}\n\n.desc {\n  margin-top: 10px;\n  display: block;\n  text-overflow: ellipsis;\n  word-wrap: break-word;\n  overflow: hidden;\n  max-height: 3.6em;\n  line-height: 1.8em;\n  text-align: justify;\n  padding-right: 10px;\n  padding-left: 10px;\n}\n\n/*\nfavorite\n*/\n\n.explore-restaurant__label {\n  font-size: 30px;\n  font-weight: 800;\n  text-align: center;\n  padding: 30px;\n\n  &:after {\n    content: \"\";\n    display: block;\n  }\n}\n\n.restaurant-item__not__found {\n  text-align: center;\n  font-size: 30px;\n}\n#explore-restaurant-list {\n  display: grid;\n  grid-row-gap: 1.5em;\n  padding-top: 1.5em;\n}\n\n.card {\n  background-color: #e9efc0;\n  border-radius: 8px;\n  box-shadow: rgba(10, 20, 30, 0.26) 0px 1px 20px;\n  margin-bottom: 20px;\n  overflow: hidden;\n  display: flex;\n  flex-direction: column;\n}\n\n.restaurant {\n  margin: 0 auto;\n  width: 100%;\n  max-width: 100%;\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 18px 16px;\n\n  &__name {\n    margin-top: 20px;\n    text-align: left;\n    text-decoration: underline;\n  }\n\n  &__image {\n    width: 100%;\n    max-width: 400px;\n    border-radius: 8px;\n    padding: 20px;\n  }\n\n  &__detail {\n    line-height: 20px;\n    display: flex;\n    padding-top: 10px;\n    padding-right: 20px;\n    flex-direction: column;\n    padding: 10px;\n\n    p {\n      text-align: justify;\n      margin-bottom: 20px;\n    }\n\n    h3 {\n      padding-top: 20px;\n    }\n\n    h4 {\n      margin: 8px 0;\n    }\n  }\n}\n\n.menu {\n  text-align: center;\n  font-size: 30px;\n  font-weight: 400;\n  padding: 10px;\n}\n\n.restaurant__overall {\n  text-align: justify;\n  padding: 10px;\n  width: calc(30% - 40px);\n  display: inline-block;\n  margin-right: 30px;\n\n  h3 {\n    font-size: 20px;\n  }\n}\n\n.restaurant__menus {\n  display: flex;\n  flex-direction: row;\n  height: 200px;\n\n  ul {\n    padding: 10px;\n    font-size: 12px;\n  }\n}\n\n.restaurant__desc__review {\n  margin-top: 15px;\n  width: 100%;\n  padding: 5px;\n  border: 3px solid black;\n}\n\n.name-review {\n  font-weight: bold;\n}\n\n.detail-review-item {\n  margin-top: 15px;\n  width: 100%;\n  padding: 5px;\n  border: 3px solid black;\n  font-size: 12px;\n}\n\n.review__overall {\n  max-width: 100%;\n  font-size: 1em;\n  text-align: left;\n}\n\n.restaurant__review h3 {\n  text-align: center;\n  font-size: 30px;\n  font-weight: 400;\n}\n\n/* FORM */\n\n.main {\n  padding: 20px;\n}\n\n.explore-restaurant__label {\n  text-align: center;\n  font-size: 40px;\n}\n\n.form-review {\n  margin-top: 30px;\n  padding: 10px;\n  border: 1px solid #ccc;\n  border-radius: 5px;\n  background-color: #f9f9f9;\n}\n\n.form-label {\n  font-weight: bold;\n  color: #333;\n}\n\n.form-control {\n  width: 100%;\n  padding: 8px;\n  margin-bottom: 15px;\n  border: 1px solid #ccc;\n  border-radius: 3px;\n  box-sizing: border-box;\n  font-size: 16px;\n  color: #555;\n  min-width: 44px;\n  min-height: 44px;\n}\n\n.btn {\n  display: inline-block;\n  background-color: #4caf50;\n  color: white;\n  padding: 8px 16px;\n  font-size: 16px;\n  border: none;\n  border-radius: 3px;\n  cursor: pointer;\n  min-width: 44px;\n  min-height: 44px;\n\n  &:hover {\n    background-color: #3e8e41;\n  }\n}\n\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #db0000;\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n\n/*\n * footer\n */\n\nfooter {\n  background-color: $footer-color;\n  padding: 2em;\n  width: 100%;\n\n  p {\n    text-align: center;\n    color: $white-color;\n    font-size: 16px;\n    font-weight: 500;\n  }\n}\n"],"sourceRoot":""}]);
